.SortableChip {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.SortableChip-info-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
}

.SortableChip-info-name {
  text-transform: uppercase;
  font-weight: 600;
  font-size: clamp(7.5px, 1vw, 8.5px);
  padding: 0 .4rem .3rem .4rem;
  line-height: normal;
  margin: 0;
}

.SortableChip-trash-btn {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  color: inherit;
  font: inherit;
  line-height: normal;
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  -webkit-appearance: none;
  -moz-appearance: none;
  outline: none;
  text-align: inherit;
  cursor: pointer;
  border-radius: 2px 0 0 0;
  transition: background .3s;
  z-index: 100;
}

.SortableChip-trash-btn svg {
  font-size: .8rem;
  margin: 3px 4px 0px 4px;
  transition: font-size .3s;
}

.SortableChip-trash-btn:hover svg {
  font-size: 1.1rem;
}